<template>

  <div class="container-lg">

    <!-- Question -->
    <div class="accordion" id="accordionPanelsStayExample">

      <div class="accordion-item question" >
        <h2 class="accordion-header" v-bind:id="'panelsStayOpen-heading'+state.qid">
          <button @click="theQuestion" class="accordion-button collapsed" type="button" data-bs-toggle="collapse" v-bind:data-bs-target="'#panelsStayOpen-collapse'+state.qid" aria-expanded="true" v-bind:aria-controls="'panelsStayOpen-collapse'+state.qid">
            질문 보기 
          </button>
        </h2>

        <div v-bind:id="'panelsStayOpen-collapse'+state.qid" class="accordion-collapse collapse" v-bind:aria-labelledby="'panelsStayOpen-heading'+state.qid">


          <div class="accordion-body">
            <div class="question-body">
              <p><b> 작성자 :: </b> {{ state.qnickname }} </p>
              <p><b>질문 제목 :: </b> {{ state.qtitle }} </p>
              <p><b>질문 내용 :: </b>{{ state.qcontent }} </p>
              <p><b>수정 일자 :: </b> {{formatDate(state.qmodDate)}} </p>
            </div>
          </div>

        </div>

      </div> <!--accordion-item question-->
    </div> <!--arccodian-->



    <!-- Answer -->
    <div class="accordion" id="accordionPanelsStayOpenExample" >

      <div class="accordion-item ">

        <h2 class="accordion-header"  v-bind:id="'panelsStayOpen-heading'">
          <h2 hidden> {{state.aid = $route.params.id }}</h2>
          <button class="accordion-button" type="button" data-bs-toggle="collapse"  v-bind:data-bs-target="'#panelsStayOpen-collapse'" aria-expanded="true"  v-bind:aria-controls="'panelsStayOpen-collapse'">
            <div class="titlecontent"> ┗ &nbsp; {{state.content}}</div>
          </button>
        </h2>

        <div  v-bind:id="'panelsStayOpen-collapse'" class="accordion-collapse"  v-bind:aria-labelledby="'panelsStayOpen-heading'">


          <div class="buttongoup">
            <el-button-group class="ml-4" v-if="state.quid == state.auid">
              <el-button class="titlebtn"  type="info" plain data-bs-toggle="modal" data-bs-target="#staticBackdrop2" @click="sendId(state.answerid, state.nickname, state.content, state.rate)"> <span> 답변 수정 </span></el-button>
              <el-button class="titlebtn"  type="info" plain  @click="deleteModal"> <span> 삭제 </span> </el-button>
            </el-button-group>

          </div>

          <!-- <div class="switch">
            <label class="publiclabel"> 공개여부 ::  </label>
            <h2 hidden>{{state.answerPublic = answer.answerPublic}}</h2>
            <el-switch  v-model="state.answerPublic" active-text="public" inactive-text="private"  />
          </div> -->

          <div class="accordion-body">
          <!-- rate answer -->
          <div class="counter">
            <div class="buttons">
              <button  v-on:click.once="state.rate >= 0 ? state.rate+=1 : state.rate+=0, sendId(state.aid, state.nickname, state.content, state.rate), modifyAnswer()"> &#708; </button>
              <button  v-on:click.once="state.rate != 0 ? state.rate-=1 : state.rate-=0, sendId(state.aid, state.nickname, state.content, state.rate), modifyAnswer()"> &#709; </button>
            </div>
              <div class="num" ref="num">
                {{ state.rate }}
              </div>
          </div>

          <div class="container">
            <!-- answer content -->
            <div class="content">
                <p> <b> 답변 작성자 :: </b>  {{ state.nickname }}</p>
                <p> <b> 답변 내용 :: </b> {{ state.content }} </p>
                <p> <b> 수정 일자 :: </b> {{formatDate(state.modDate)}}</p>
            </div>
          </div>
        </div>

        <!-- Comment -->
        <div class="commentList">
          <PrivateCommmentread
            :answerid="state.aid"
            :quid="state.quid"
            :qemail="state.email"
            />
        </div>


        </div>




      </div><!--accordion-item-->
    </div><!--arccodian-->


    <!-- Modal -->
    <div class="modal fade" id="staticBackdrop2" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="staticBackdropLabel">Answer Modify</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>

          <div class="modal-body modalcontainer">

            <div class="item modalswitch">
              <label for="nickname"> 공개여부 :: </label><br>
              <el-switch v-model="answerPublic" size="large" active-text="Public" />
            </div>

            <div class="item">
              <label for="nickname"> Nickname :: </label><br>
              <input type="text" id="nickname" v-model="modal.nickname" disabled>
            </div>

            <div class="item">
              <label for="content"> Content :: </label><br>
              <textarea type="text" id="content" v-model="modal.content" ></textarea>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
            <button type="button" class="btn btn-primary"  @click="modifyAnswer()" data-bs-dismiss="modal">Modify</button>
          </div>
        </div>
      </div>
    </div>



  </div>



</template>

<script>
  import { ElMessage, ElMessageBox } from 'element-plus'
  import { onMounted, ref } from 'vue'
  import { reactive, } from 'vue'
  import {useRouter} from 'vue-router';
  import axios from 'axios'
  import dayjs from 'dayjs'
  import PrivateCommmentread from './PrivateCommmentread.vue'

export default {
  name: "TheAnswer",
  components: { PrivateCommmentread },

  setup(){

  onMounted(() => { answerRead(), registerUid() })

  const router = useRouter()
  const answerPublic = ref(true)

  const formatDate = (dateString) => {
    const date = dayjs(dateString);
    return date.format('YYYY-MM-D HH:mm');
  };

  const state = reactive({
      qid : '',
      qnickname : '',
      quid: '',
      qtitle:'',
      qcontent:'',
      aid : '',
      auid : '',
      rate:'',
      nickname :'',
      answerPublic:'',
      modDate:'',
      lists: '',
      countComment:'',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
      showModal   : false,
    });

  const modal = reactive({
    answerid:'',
    nickname:'',
    content:'',
    rate:''
  })

  const sendId = (answerid, nickname, content, rate) =>{
    modal.answerid = answerid
    modal.nickname = nickname
    modal.content = content
    modal.rate = rate
  };

  const deleteModal= () => {
    ElMessageBox.confirm(
      'Do you really want to delete your answer?',
      'Warning',
      {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning',
      }
    )
      .then(() => {
        ElMessage({
          type: 'success',
          message: 'Delete completed',
        })
        deleteAnswer()
      })
      .catch(() => {
        ElMessage({
          type: 'info',
          message: 'Delete canceled',
        })
      })
    }

    const answerRead = async () => {
    const url= "http://43.201.60.125/api/answerread"

    const headers = {
      //TODO send the right amound of tokent and receive the same...
      'Content-Type' : 'application/json',
      Authorization: state.token,
      token: state.token,
      'name': 'test list'
    }
      const body = { answerid: state.aid }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.status==200){
          state.qid = res.data.dto.questionid
          state.auid = res.data.dto.uid
          state.nickname = res.data.dto.nickname
          state.content = res.data.dto.content
          state.rate = res.data.dto.rate
          state.modDate = res.data.dto.modDate
        } else{
          alert('bad.')
        }
      })
    }

     const registerUid = async () => {   // 글 등록시 로그인한 사용자가 맞는지 확인용
    const url= "http://43.201.60.125/api/answerregiuid"
    const headers = {
      //TODO send the right amound of tokent and receive the same...
      'Content-Type' : 'application/json',
      Authorization: state.token,
      token: state.token,
      'name': 'test list'
    }
      const body = { email:state.email }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.status==200){
          state.quid = res.data
        } else {
          alert('bad.')
        }
      })
    }

     const theQuestion = async () => {
      const url= "http://43.201.60.125/api/questionread"
    
    const headers = {
      //TODO send the right amound of tokent and receive the same...
      'Content-Type' : 'application/json',
      Authorization: state.token,
      token: state.token }
      const body = { questionid: state.qid }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.status===200){
          state.circleid = res.data.dto.circleid
          state.qnickname = res.data.dto.nickname
          state.qtitle = res.data.dto.title
          state.qcontent = res.data.dto.content
          state.questionPublic = res.data.dto.questionPublic
          state.modDate = res.data.dto.modDate
          state.quid = res.data.dto.uid
        } else {
          alert('bad.')
        }
      })
    }

    const deleteAnswer = async()=>{
    const url ='http://43.201.60.125/api/deleteAnswer'
    const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token }
        const body ={answerid : state.aid,}
        console.log("delete answer... launch  " +state.aid)
    await axios.post(url, body, {headers}).then(function(res){
      console.log("delete answer after axios" )
      if(res.status === 200){
        router.go(0)
        console.log(`answer${state.aid} is deleted`);
      }
    })
  }

  const modifyAnswer = async() =>{
      if(modal.content ===''){
        alert("Please input content");modal.content.value.focus();return false;
      }
      console.log("modify Answer launch")
      const url = "http://43.201.60.125/api/privateanswermodify"
      const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        answerid: state.aid,
        nickname: state.nickname,
        content: modal.content,
        answerPublic: answerPublic.value,
        rate: modal.rate
      }
      const response = await axios.post(url, body, {headers})
      if(response.status ===200){
        alert('modify successfully')
        setTimeout( answerRead(), 1000)
      } else{
        alert('fail')
      }
    }




    return{ formatDate, answerPublic, deleteModal, state, answerRead, registerUid, theQuestion, deleteAnswer, modifyAnswer, modal, sendId  }

  }

};

</script>

<style scoped>

.question-body{
  display: block;
  text-align: left;
  margin:50px;
}

.btnreg{
  display: block;
  text-align: right;
  margin:10px;
}

.accordion-button {
  background: rgba(6, 90, 92, 0.2);
  color: rgba(0,0,0,0.8)
}
.accordion-button:hover{
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}
.accordion-button:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}

button {
  font-size: 20px;
  font-weight: bold;
}

.titlecontent{
  display: block;
  width: 350px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}

.accordion-body{
  display: flex;
}

.counter{
  display:  flex;
  align-items: center;
  height: 80px;
  margin-right: 30px;
  background-color:lightgray;
  padding: 0 10px;
  border-radius: 30%;
}

.num{
  font-size: 30px;
  color: rgb(6, 90, 92);
  font-weight: bold;
  margin: 10px;
}

.buttongoup {
  display: block;
  text-align: right;
  margin:10px;
}

span{
  font-size: 15px;
}

.container{
  display: block;
  text-align: left;
}

.switch{
  display: block;
  text-align: right;
  margin:10px;
}



.buttons button {
  font-size: 15px;
  display: block;
  margin: 0 auto;
  height: 10x;
  border: none;
  border-radius: 10px;
}

.modalcontainer{
  display: block;
  text-align: left;
  width: 500px;
  height: 100%px;
}

.publiclabel {
  margin: 10px;
}

label{ margin:10px 0; }
textarea{
  width:400px;
  height: 180px;
}


</style>>

