<template>
  <div class="btnreg">
    <el-button plain @click="state.showModal=true"> 댓글 등록하기 </el-button>
  </div>

  <div class="card" v-for="(comment, i) in state.lists" :key="i" >
    <div hidden> {{state.cid = comment.commentid}}</div>
    <div hidden> {{state.content = comment.content}}</div>




    <div class="buttongoup">

      <el-button-group class="ml-4" v-if="state.quid == comment.uid">
        <el-button plain  @click="deleteModal"> 삭제하기 </el-button>
      </el-button-group>
    </div>

    <div class="card-body container" v-if="comment.uid != null">
      <div class="item1"> <b> 댓글 작성자 </b> {{comment.nickname}} </div>
      <div class="item2">
        <p> <b>댓글 내용 :: </b> {{comment.content}} </p>
        <p> <b>수정 일자 :: </b> {{formatDate(comment.modDate)}}  </p>
        <p hidden> {{state.cid = comment.commentid}}  </p>
      </div>
    </div>

  </div>


  <!-- Register -->
  <Teleport to="body">
    <!-- use the modal component, pass in the prop -->
    <modal :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h3>Register Comment</h3>
      </template>
      <template #body>

        <div class="item">
          <label for="nickname"> Email :: </label><br>
          <input type="text" id="nickname" v-model="state.qemail" disabled>
        </div>
        <div class="item">
          <label for="content"> Content :: </label><br>
          <textarea type="text" id="content" v-model="regist.registContent"></textarea>
        </div>

      </template>
      <template #footer>
        <el-button type="secondary" plain @click="state.showModal=false"> Close </el-button>
        <el-button type="primary" plain @click="commentRegister()">Register</el-button>
      </template>
    </modal>
  </Teleport>


</template>

<script>
  import { ElMessage, ElMessageBox } from 'element-plus'
  import { onMounted, toRef, ref } from 'vue'
  import { reactive, } from 'vue'
  import {useRouter} from 'vue-router';
  import Modal from './teleport/Modal.vue'
  import dayjs from 'dayjs'
  import axios from 'axios'

export default {
  name: "TheComment",
  props: [ 'answerid', 'quid', 'qemail'],
  components: { Modal, },


  setup(props){

    onMounted(() => { commentRead() })

    const router = useRouter()
    const aid = toRef(props,'answerid')
    const quid = toRef(props, 'quid')
    const qemail = toRef(props, 'qemail')

    const state = reactive({
      aid : aid,
      qnickname : '', //댓글 추가시 로그인된 nickname 보여짐
      quid:quid ,     //댓글 추가시 로그인된 uid로 댓글 추가
      qemail:qemail,
      cid  : '',
      nickname: '',
      content:'',
      lists: '',
      modDate:'',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
      showModal   : false,
    });


    const formatDate = (dateString) => {
    const date = dayjs(dateString);
    return date.format('YYYY-MM-D HH:mm');
    };

  

    const deleteModal = () => {
    ElMessageBox.confirm(
      'Do you really want to delete your comment?',
      'Warning',
      {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning',
      }
    )
      .then(() => {
        ElMessage({
          type: 'success',
          message: 'Delete completed',
        })
        deleteComment()
      })
      .catch(() => {
        ElMessage({
          type: 'info',
          message: 'Delete canceled',
        })
      })
    }


    const commentRead = async () => {
    const url= "http://43.201.60.125/api/commentbyanswer"

    const headers = {
      //TODO send the right amound of tokent and receive the same...
      'Content-Type' : 'application/json',
      Authorization: state.token,
      token: state.token,
      'name': 'test list'
    }
      const body = { answer: state.aid }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.status==200){
          state.lists = res.data

        } else {
          alert('bad.')
        }
      })
    }

    const deleteComment = async()=>{
    const url ='http://43.201.60.125/api/deleteComment'
    const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token }
        const body ={commentid : state.cid,}
    await axios.post(url, body, {headers}).then(function(res){
      if(res.status === 200){
        setTimeout(commentRead(),1000);
        }
      })
    }



    const regist = reactive({
      registContent:'',
    });

    const commentRegister = async() => {
        if(regist.registContent === ''){
          alert('Please Input your content');email.value.focus(); return false;
        }
        const url = 'http://43.201.60.125/api/privatecmregister'
        const headers = {
          "Content-Type" : "application/json",
          Authorization: state.token,
          "token": state.token
        }
        const body = {
          answerid : state.aid,
          uid : state.quid,
          content : regist.registContent,
        }
        const response = await axios.post(url, body, {headers})
        // console.log(response.data)
        if(response.status === 200){
          alert('register successfully');
          state.showModal=false;
          setTimeout(commentRead(),1000);
        } else {
          alert('fail')
        }
      }



    return{ formatDate, deleteModal,  state, commentRead, deleteComment, regist, commentRegister }

  }


};

</script>

<style scoped>

.container{
  display: grid;
  grid-template-columns: 1fr 3fr;
}

.btnreg{
  display: block;
  text-align: right;
  margin:10px;
}
 .buttongoup{
  display: block;
  margin: 10px;
  margin-top: 10px;
  text-align: right;
}

.item1{
  border-right: 1px solid gray;
  margin-right: 10px;
  padding-right: 10px;
}

.item2{
  display: block;
  text-align: left;
}

.modalcontainer{
    display: block;
    text-align: left;
    width: 500px;
    height: 100%px;
}

label{ margin:10px 0; }
textarea{
  width:400px;
  height: 100px;
}

</style>
